<script>
import myPreview from '../components/preview.vue'

export default {
  components: { myPreview },
  data () {
    return {
      showPreview1: false,
      showPreview2: false,
      showPreview3: false,
      showPreview4: false,
      showPreview5: false,
      showPreview6: false,
      showPreview7: false
    }
  },
  methods: {
    bound () {
      const lists = document.querySelectorAll('.item')
      const liLength = lists.length
      const view = document.querySelectorAll('.previewA')
      for (let i = 0; i < liLength; i++) {
        const rect = lists[i].getBoundingClientRect()
        view[i].style.left = (rect.x - 300) + 'px'
        view[i].style.top = (rect.top - (19 * i)) + 'px'
      }
      window.addEventListener('resize', this.resize)
    },
    resize () {
      const lists = document.querySelectorAll('.item')
      const liLength = lists.length
      const view = document.querySelectorAll('.previewA')
      for (let i = 0; i < liLength; i++) {
        const rect = lists[i].getBoundingClientRect()
        view[i].style.left = (rect.x - 300) + 'px'
        view[i].style.top = rect.y + 'px'
      }
    }
  },
  mounted () {
    this.bound()
  }
}
</script>

