<template>
    <div v-if="show" :class="preview">
        <div class=previewImage>
          <img :src=pic_src alt="">
        </div>
        <h3 v-if="title" class="title">{{title}}</h3>
        <div class="com">
            <span>구성</span>
            <p>
                <span>메인 페이지</span>
                <span>서브 페이지</span>
            </p>
        </div>
        <div class="use">
            <span>태그</span>
            <slot></slot>
        </div>
    </div>
</template>

<script>
export default {
  name: 'myPreview',
  props: {
    show: Boolean,
    title: String,
    preview: String,
    pic_src: String
  },
  data () {
    const views = document.querySelectorAll('.preview')
    return {
      views
    }
  }
}
</script>

